.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  width: 96px;
  box-sizing: content-box;
  height: 48px;
  background: #fff;
  border: 2px solid #de3500;
  border-bottom-width: 50px;
  border-radius: 100%;
  animation: yinYang 3s linear infinite;
  position: relative;
}

.loader:before {
  content: "";
  width: 12px;
  height: 12px;
  box-sizing: content-box;
  background: #fff;
  border: 18px solid #de3500;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 0;
}

.loader:after {
  content: "";
  width: 12px;
  height: 12px;
  box-sizing: content-box;
  background: #de3500;
  border: 18px solid #fff;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes yinYang {
  100% {
    transform: rotate(360deg);
  }
}

